"use client";

import React, { useState } from "react";
import { MapPin, Calendar, MoreVertical, X, Shield, ShieldIcon } from "lucide-react";

interface ChatUser {
    id: number;
    name: string;
    avatar: string;
    avatarColor: string;
    lastMessage: string;
    timestamp: string;
    hasImage?: boolean;
}

interface Message {
    id: number;
    type: "product" | "text" | "negotiation";
    content?: string;
    productName?: string;
    productPrice?: string;
    productImage?: boolean;
    originalPrice?: string;
    newPrice?: string;
    status?: string;
}

const ChatPage = () => {
    const [selectedChat, setSelectedChat] = useState<number>(2);

    const chatUsers: ChatUser[] = [
        {
            id: 1,
            name: "CashNCarry",
            avatar: "CC",
            avatarColor: "bg-blue-200 text-blue-700",
            lastMessage: "Women vintage sweat bla...",
            timestamp: "2 days ago",
        },
        {
            id: 2,
            name: "Offizy Stores",
            avatar: "OS",
            avatarColor: "bg-purple-200 text-purple-700",
            lastMessage: "Beachwear light top black...",
            timestamp: "2 days ago",
            hasImage: true,
        },
        {
            id: 3,
            name: "Wearables",
            avatar: "W",
            avatarColor: "bg-green-200 text-green-700",
            lastMessage: "Vintage Jean suede black...",
            timestamp: "2 days ago",
            hasImage: true,
        },
    ];

    const messages: Message[] = [
        {
            id: 1,
            type: "product",
            productName: "Beachwear light top black men's shirt",
            productPrice: "$20.50",
            productImage: true,
        },
        {
            id: 2,
            type: "text",
            content: "Hello, welcome to Offizy Stores\nWhat would you want us to do for you, today?",
        },
        {
            id: 3,
            type: "negotiation",
            originalPrice: "$20.50",
            newPrice: "$15.00",
            status: "Negotiation accepted",
        },
    ];

    return (
        <div className="flex">
            {/* Sidebar - Chat List */}
            <div className="w-80 bg-white border-r border-gray-200 rounded-tl-[18px] rounded-bl-[18px] border border-gray-100">
                <div className="p-6 border-b border-gray-200">
                    <h1 className="text-2xl font-semibold text-gray-900">Chat</h1>
                </div>

                <div className="overflow-y-auto">
                    {chatUsers.map((user) => (
                        <div
                            key={user.id}
                            onClick={() => setSelectedChat(user.id)}
                            className={`flex items-center gap-3 p-4 border-b border-gray-100 cursor-pointer hover:bg-gray-50 transition-colors ${selectedChat === user.id ? "bg-gray-50" : ""
                                }`}
                        >
                            <div
                                className={`w-12 h-12 rounded-full ${user.avatarColor} flex items-center justify-center font-semibold text-lg flex-shrink-0`}
                            >
                                {user.avatar}
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center justify-between mb-1">
                                    <h3 className="font-semibold text-gray-900 truncate">
                                        {user.name}
                                    </h3>
                                    <span className="text-xs text-gray-500 flex-shrink-0 ml-2">
                                        {user.timestamp}
                                    </span>
                                </div>
                                <div className="flex items-center gap-2">
                                    {user.hasImage && (
                                        <div className="w-8 h-8 bg-gray-200 rounded flex-shrink-0"></div>
                                    )}
                                    <p className="text-sm text-gray-600 truncate">
                                        {user.lastMessage}
                                    </p>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Main Chat Area */}
            <div className="flex-1 flex flex-col rounded-tr-[18px] rounded-br-[18px] border border-gray-100">
                {/* Chat Header */}
                <div className="bg-white border-b border-gray-200 p-4 flex items-center justify-between">
                    <div className="flex items-center gap-3">
                        <div className="w-12 h-12 rounded-full bg-purple-200 text-purple-700 flex items-center justify-center font-semibold text-lg">
                            OS
                        </div>
                        <div>
                            <h2 className="font-semibold text-gray-900">Offizy Stores</h2>
                            <div className="flex items-center gap-1 text-xs text-gray-500">
                                <div className="w-2 h-2 bg-gray-400 rounded-full"></div>
                                <span>Offline (Last active - 2mins ago)</span>
                            </div>
                        </div>
                    </div>
                    <button className="p-2 hover:bg-gray-100 rounded-lg transition-colors">
                        <MoreVertical className="w-5 h-5 text-gray-600" />
                    </button>
                </div>

                {/* Messages Area */}
                <div className="flex-1 overflow-y-auto p-6 space-y-4">
                    {messages.map((message) => (
                        <div key={message.id}>
                            {message.type === "product" && (
                                <div className="flex items-center gap-3 bg-gray-100 rounded-2xl p-3 max-w-sm">
                                    <div className="w-16 h-16 bg-gray-300 rounded-lg flex-shrink-0"></div>
                                    <div>
                                        <p className="text-sm font-medium text-gray-900 mb-1">
                                            {message.productName}
                                        </p>
                                        <p className="text-sm font-semibold text-gray-900">
                                            {message.productPrice}
                                        </p>
                                    </div>
                                </div>
                            )}

                            {message.type === "text" && (
                                <div className="max-w-md">
                                    <div className="bg-gray-100 rounded-2xl rounded-tl-sm p-4 mb-2">
                                        <p className="text-sm text-gray-900 whitespace-pre-line">
                                            {message.content}
                                        </p>
                                    </div>
                                    <div className="flex items-center gap-3 text-xs text-gray-500 px-2">
                                        <div className="flex items-center gap-1">
                                            <MapPin className="w-3 h-3" />
                                            <span>Abuja, Nigeria</span>
                                        </div>
                                        <div className="flex items-center gap-1">
                                            <Calendar className="w-3 h-3" />
                                            <span>Last active - 2mins ago</span>
                                        </div>
                                    </div>
                                </div>
                            )}

                            {message.type === "negotiation" && (
                                <div className="flex justify-end">
                                    <div className="bg-green-100 border border-green-200 rounded-2xl rounded-br-sm p-4 max-w-xs">
                                        <div className="flex items-center gap-3 mb-1">
                                            <span className="text-lg font-bold text-green-700">
                                                {message.newPrice}
                                            </span>
                                            <span className="text-sm text-gray-500 line-through">
                                                {message.originalPrice}
                                            </span>
                                        </div>
                                        <p className="text-xs text-green-700 font-medium">
                                            {message.status}
                                        </p>
                                    </div>
                                </div>
                            )}
                        </div>
                    ))}

                    {/* Proceed To Buy Button */}
                    <div>
                        <button className="bg-emerald-900 hover:bg-emerald-950 text-white px-8 py-3 rounded-full font-medium transition-colors">
                            Proceed To Buy
                        </button>
                    </div>
                </div>

                {/* Safety Notice */}
                <div className="bg-[#F6FAF9] border-t border-gray-200 p-4">
                    <div className="flex items-center justify-between max-w-4xl mx-auto">
                        <div className="flex items-center gap-2">
                            <ShieldIcon />
                            <p className="text-sm text-gray-700">
                                Stay safe on FLEZTA. Don't share personal data, click on external links, or scan QR codes.
                            </p>
                        </div>
                        <button className="p-1 hover:bg-gray-200 rounded transition-colors">
                            <X className="w-5 h-5 text-gray-600" />
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ChatPage;